.actions-menu__list {
  display: flex;
  flex-direction: row;
  gap: 3rem;
}

.actions-menu__item:hover .icon,
.actions-menu__item:hover .search-icon,
.actions-menu__item:hover .actions-menu__amount {
  color: var(--secondary-color);
}

.actions-menu__link {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-decoration: none;
}

.actions-menu__quantity {
  width: 2.4rem;
  height: 2.4rem;
  font-family: "Nunito Sans", Arial, sans-serif;
  font-size: 1.3rem;
  font-weight: 800;
  border-radius: 50%;
  color: var(--white-color);
  background-color: var(--secondary-color);

  display: flex;
  display: -webkit-box;
  display: -ms-flexbox;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  -webkit-box-align: center;
  -webkit-box-pack: center;
  -ms-flex-align: center;
  -webkit-box-orient: vertical;
  -ms-flex-pack: center;

  position: absolute;
  top: -0.4rem;
  left: 1.8rem;
  z-index: 2;
}

.icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.2rem;
  height: 3.2rem;
  color: var(--base-color);
  -webkit-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}

.icon__svg {
  fill: currentColor;
  display: block;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
}
