.checkbox {
  padding: 0.5rem 0;
  font-family: "Nunito Sans", Arial, sans-serif;
  font-size: 2rem;
  font-weight: 600;
  line-height: 4rem;
  display: inline-block;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  z-index: 1;
}

.checkbox__input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}

.checkbox__wrapper {
  display: flex;
  max-width: 100%;
  position: relative;
  cursor: pointer;
  align-items: center;
}

.checkbox__icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.8rem;
  height: 2.8rem;
  margin-right: 1rem;
  color: var(--grey-color);
}

.checkbox__icon::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  display: block;
  background-color: var(--grey-light-color);
  border-radius: 50%;
  flex-shrink: 0;
  z-index: -1;
  transition: opacity 0.3s linear, width 0.3s linear, height 0.3s linear,
    background-color 0.3s linear;
  opacity: 0;
}

.checkbox__icon::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 2.4rem;
  height: 2.4rem;
  border: 0.3rem solid var(--grey-color);
  border-radius: 0.5rem;
  z-index: -1;
  transition: all 0.3s ease-in-out;
}

.checkbox__svg {
  fill: currentColor;
  display: block;
  width: 2.4rem;
  height: 2.4rem;
  flex-shrink: 0;
  transition: opacity 0.3s ease-in-out;
  opacity: 0;
}

.checkbox:hover .checkbox__svg {
  opacity: 1;
}

.checkbox:hover .checkbox__icon::before {
  width: 3.6rem;
  height: 3.6rem;
  opacity: 1;
}

.checkbox__input:checked ~ .checkbox__wrapper .checkbox__svg {
  opacity: 1;
}

.checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon {
  color: #fff;
}

.checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: #ffe9ee;
}

.checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  border-color: #fd7898;
  background-color: #fd7898;
}

.checkbox_colors {
  padding: 0;
  border-radius: 50%;
}

.checkbox_colors .checkbox__icon {
  margin: 0;
  color: var(--white-color);
}

.checkbox_colors .checkbox__icon:after {
  border: 0;
  border-radius: 50%;
}

.checkbox_blue {
  background-color: var(--blue-color);
}

.checkbox_blue:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(55, 139, 235, 0.3);
}

.checkbox_blue .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--blue-color);
}

.checkbox_red {
  background-color: var(--red-color);
}

.checkbox_red:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(245, 59, 59, 0.3);
}

.checkbox_red .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--red-color);
}

.checkbox_green {
  background-color: var(--green-color);
}

.checkbox_green:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(61, 238, 111, 0.3);
}

.checkbox_green .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--green-color);
}

.checkbox_yellow {
  background-color: var(--yellow-color);
}

.checkbox_yellow:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(252, 234, 72, 0.3);
}

.checkbox_yellow .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--yellow-color);
}

.checkbox_purple {
  background-color: var(--purple-color);
}

.checkbox_purple:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(182, 109, 240, 0.3);
}

.checkbox_purple .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--purple-color);
}

.checkbox_black {
  background-color: var(--black-color);
}

.checkbox_black:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(0, 0, 0, 0.3);
}

.checkbox_black .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--black-color);
}

.checkbox_grey {
  background-color: var(--grey-color);
}

.checkbox_grey:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(180, 180, 180, 0.3);
}

.checkbox_grey .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--grey-color);
}

.checkbox_white {
  background-color: var(--white-color);
  border: 0.01rem solid var(--grey-color);
}

.checkbox_white:hover .checkbox__input ~ .checkbox__wrapper .checkbox__icon::before {
  background-color: rgba(180, 180, 180, 0.3);
}

.checkbox_white .checkbox__icon {
  color: var(--grey-color);
}

.checkbox_white .checkbox__icon::after {
  background-color: var(--white-color);
}

.checkbox_white .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon {
  color: var(--grey-color);
}

.checkbox_white .checkbox__input:checked ~ .checkbox__wrapper .checkbox__icon::after {
  background-color: var(--white-color);
}
